import React, { useState } from 'react';
import axios from '../api/api';
import ReCAPTCHA from 'react-google-recaptcha'; // Th vin reCAPTCHA
import './Contact.css';
const Contact = () => {
  const [formData, setFormData] = useState({ name: '', email: '', phone: '', company: '', address: '', message: '' });
  const [captchaToken, setCaptchaToken] = useState('');
  const [submitDisabled, setSubmitDisabled] = useState(false); // Kha submit trong vng 1 pht

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleCaptchaChange = (token) => {
    setCaptchaToken(token); // Lu m token khi CAPTCHA c xc nhn
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!captchaToken) {
      alert('Please complete the CAPTCHA verification');
      return;
    }

    try {
      const response = await axios.post('/contacts', { ...formData, captchaToken });
      alert('Your message has been sent!');
      setFormData({ name: '', email: '', phone: '', company: '', address: '', message: '' }); // Reset form
      setCaptchaToken(''); // Reset CAPTCHA
      setSubmitDisabled(true); // Kha submit
      setTimeout(() => setSubmitDisabled(false), 60000); // M kha sau 1 pht
    } catch (error) {
      console.error('Error sending message:', error);
      alert('Failed to send message. Please try again later.');
    }
  };

  return (
<div className="contact-container">

      <p>Vui lng  li thng tin dch v bn quan tm hoc  kin.
      Chng ti s lin lc v tr li sm nht.</p>
      <p>
        Bng cch nhp vo "Gi tin nhn", ti tha nhn v hiu rng thng tin c nhn ca ti c th
        c thu thp v s dng nh c m t trong Chnh sch quyn ring t ca Bak, bao gm gi cho ti
        thng tin lin lc v cc sn phm, dch v v s kin ca Bak.
      </p>
      <p><strong>Lin h:</strong> +84 24 33547545</p>
      <p><strong>a ch:</strong> TT19 A26, khu  th Vn Qun, H ng, H Ni</p>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label>Name</label>
          <input type="text" name="name" className="form-control" onChange={handleChange} required />
        </div>
        <div className="form-group">
          <label>Email</label>
          <input type="email" name="email" className="form-control" onChange={handleChange} required />
        </div>
        <div className="form-group">
          <label>Phone</label>
          <input type="tel" name="phone" className="form-control" onChange={handleChange} required />
        </div>
        <div className="form-group">
          <label>Company</label>
          <input type="text" name="company" className="form-control" onChange={handleChange} />
        </div>
        <div className="form-group">
          <label>Address</label>
          <input type="text" name="address" className="form-control" onChange={handleChange} />
        </div>
        <div className="form-group">
          <label>Message</label>
          <textarea name="message" className="form-control" onChange={handleChange} required />
        </div>

        {/* CAPTCHA */}
        <ReCAPTCHA
          sitekey="6Lc402sqAAAAAPWoGiRFWzPDaMJ0oe3aTxhnK8om" // Thay bng m site key ca bn
          onChange={handleCaptchaChange}
        />

        <button type="submit" className="btn btn-primary" disabled={submitDisabled}>Send</button>
      </form>
    </div>
  );
};

export default Contact;
